// TODO: Refactor for universal use with candidate and employer scenarios. Ralf @ 1/19/18 11:44 AM
import React, { Component } from 'react'
import { compose } from 'redux'
import PropTypes from 'prop-types'
import Dropzone from 'react-dropzone'
import { Interpolate } from 'react-i18next'
import notie from 'notie'
import { pick } from 'ramda'
import NotificationService from 'Shared/services/NotificationService'
import { getAcceptedFormats, MAX_FILE_COUNT, MAX_UPLOAD_FILE_SIZE, ACCEPTED_CV_FORMATS } from 'Shared/constants/S3'
import withTranslations from 'Shared/hoc/withTranslations'
import { isFileNameTaken } from 'Shared/helpers/files'
import './S3Uploader.scss'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'
import Label from 'Shared/components/FormElements/Label'
import { t } from 'i18next'

class S3Uploader extends Component {
  static propTypes = {
    uploadFile: PropTypes.func.isRequired,
    generateSignature: PropTypes.func.isRequired,
    maxSize: PropTypes.number,
    maxFileCount: PropTypes.number,
    requirement: PropTypes.bool,
    handleException: PropTypes.func,
    formats: PropTypes.string
  }

  static defaultProps = {
    maxFileCount: MAX_FILE_COUNT,
    formats: getAcceptedFormats(),
    requirement: true,
    label: t('modals.uploadFile.title'),
    placeholder: t('modals.uploadFile.title')
  }

  dropzoneRef = {}

  state = {
    files: []
  }

  _uploadFiles = async (filesToUpload) => {
    if (!this.props.requirement) {
      return this.props.handleException()
    }
    const { maxFileCount, files, allFiles, t } = this.props
    const canUploadFiles = files.length >= maxFileCount || (filesToUpload.length + files.length) > maxFileCount

    if (filesToUpload.length > 0 && filesToUpload.some(file => !ACCEPTED_CV_FORMATS.includes(file.type))) {
      return NotificationService.error(t('errors.fileUnsupported'))
    }

    if (canUploadFiles) {
      return notie.alert({
        type: 'info',
        text: t('alerts.maxFileCount', { count: maxFileCount })
      })
    }

    const filesToCompare = allFiles || files

    filesToUpload.forEach(async file => {
      if (isFileNameTaken(filesToCompare, file)) {
        return NotificationService.info(this.props.t('alerts.fileExists'))
      } else {
        const { policy, signature } = await this.props.generateSignature()
        return this.props.uploadFile(file, policy, signature)
      }
    })
  }

  handleReject = (rejectedFiles) => {
    if (rejectedFiles.length > 0 && rejectedFiles.some(file => !ACCEPTED_CV_FORMATS.includes(file.type))) {
      return NotificationService.error(t('errors.fileUnsupported'))
    }

    if (rejectedFiles.length > 0 && rejectedFiles.some(file => file.size > MAX_UPLOAD_FILE_SIZE)) {
      return NotificationService.error(this.props.t('alerts.fileToLarge', { limit: MAX_UPLOAD_FILE_SIZE / Math.pow(1024, 2) }))
    }
    return NotificationService.error(this.props.t('alerts.fileRejected'))
  }

  _fileInputProps = () => pick(['maxSize'], this.props)

  uploadButton = () => <span className='file-input__button--upload' onClick={() => this.dropzoneRef.open()}>{this.props.t('common.choose')}</span>

  render () {
    const { label, placeholder } = this.props

    return this.props.isMobile ? (
      <div className='file-input-mobile'>
        <Dropzone
          {...this.props.input}
          {...this._fileInputProps()}
          accept={this.props.formats}
          onDropAccepted={this._uploadFiles}
          onDropRejected={this.handleReject}
          className='file-input-mobile__dropzone'
          activeClassName='file-input-mobile__dropzone--active'
          disableClick
          ref={(node) => {
            this.dropzoneRef = node
          }}
          onDrop={this.onDrop}
        >
          <Label>{label}</Label>
          <div className='file-input-mobile__container' onClick={() => this.dropzoneRef.open()}>
            <span className='file-input-mobile__placeholder'>{placeholder}</span>
            <div className='file-input-mobile__icon'>
              <i className='icon-im-upload' />
            </div>
          </div>
        </Dropzone>
      </div>
    ) : (
      <div className='file-input'>
        <Dropzone
          {...this.props.input}
          {...this._fileInputProps()}
          accept={this.props.formats}
          onDropAccepted={this._uploadFiles}
          onDropRejected={this.handleReject}
          className='file-input__dropzone'
          activeClassName='file-input__dropzone--active'
          disableClick
          ref={(node) => {
            this.dropzoneRef = node
          }}
          onDrop={this.onDrop}
        >
          <div className='file-input__container'>
            <div className='file-input__drop-area'>
              <i className='icon-im-upload' />
              <Interpolate
                parent='div'
                i18nKey='common.uploadFileInfo'
                className='file-input__description'
                uploadButton={this.uploadButton()}
              />
            </div>
          </div>
        </Dropzone>
      </div>
    )
  }
}

export default compose(
  withTranslations,
  withDeviceWidthDetector
)(S3Uploader)



